.background-about-us{
    background:#ca8d65;
    padding: 3.5rem 3rem;
}

.header-about-us{
    margin:0px;
    text-align: center;
    font-size: 2.5rem;
}
.content-about-us{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    text-align: center;
    font-weight: 400;
    font-size: 1.5rem;
}
@media (max-width:48rem) {
    .header-about-us{
      font-size: 2rem;
    }
    .content-about-us{
        font-size: 1.25rem;
    }
  }
@media (max-width:28rem) {
    .header-about-us{
      font-size: 1.75rem;
    }
    .content-about-us{
        font-size: 1rem;
    }
  }